body, html{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    width:100vw;
    display:flex;
    justify-content:center;
}

* {
    box-sizing: border-box;
  }

.page-container {
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: fit-content;
    position: relative;
  }

:root{
    --pr-color:#32cd32;
    --sc-color:rgb(80, 128, 96);
}

/* Base styles for the div */
.app {
  width: 100%;
  background-color: #f9f9f9;
  min-height:100dvh;
}

/* Styles for screens larger than laptops (1024px and up) */
@media (min-width: 1024px) {
  .app {
    width: 500px;
    border:1px solid black;
  }
}
